import { useRef } from "react";
import { Route, Switch } from "react-router-dom";
import "./App.scss";

import Header from './components/Header/Header';
import Cover from './components/Cover/Cover';
import Services from './components/Services/Services';
import TextSection from './components/TextSection/TextSection';
import Gallery from './components/Gallery/Gallery';
import ContactsSection from './components/ContactsSection/ContactsSection';
import Footer from './components/Footer/Footer';
import UIControls from "./components/UIControls/UIControls";
// import Overlay from "./components//Overlay/Overlay";

import textAboutDoctor from "./data/text-about-doctor.json";
import textEducation from "./data/text-education.json";

function App() {
  const refs = useRef({});

  return (
    <div className="App">
      <Header navRefs={refs.current} />
      <main className="main">
        <Switch>
          <Route exact path='/'>
            <Cover ref={refs.current} navId="home" navRefs={refs.current} />
            <Services ref={refs.current} navId="services" />
            <TextSection ref={refs.current} navId="about_doctor" textData={textAboutDoctor} columns />
            <TextSection ref={refs.current} navId="my_education" textData={textEducation} />
            <Gallery ref={refs.current} navId="my_certificates" />
            <ContactsSection ref={refs.current} navId="book_appointment" />
            <Footer />
          </Route>

          {/* <Route path='/signup'>
            <Signup />
          </Route> */}

          <Route path="/ui-development">
            <UIControls />
          </Route>
        </Switch>
      </main>
    </div>
  );
}

export default App;
