import cn from "classnames";
import { NavLink, useLocation } from "react-router-dom";

export const ScrollNavLink = function ({ classNames, activeClassName, navRefs, navId, offsetTop, children }) {
  const location = useLocation();

  function onClick() {
    if (offsetTop) {
      const headerOffset = 123.60;
      const elementPosition = navRefs[navId]?.getBoundingClientRect().top;
      const offsetPosition = elementPosition + window.pageYOffset - headerOffset;
    
      window.scrollTo({
           top: offsetPosition,
           behavior: "smooth"
      });
      return;
    }
    navRefs[navId]?.scrollIntoView({ behavior: "smooth", block: "center", inline: "center" });
  }
  return <NavLink
    to={`/#${navId}`}
    isActive={() => location.pathname + location.hash === `/#${navId}`}
    className={cn(classNames, 'link-unstyling')}
    onClick={onClick}
    activeClassName={activeClassName}
  >
    {children}
  </NavLink>;
}


