import { useEffect } from "react";
import useElemOnScreen from "./useElemOnScreen";
import { useHistory } from "react-router-dom";

function useUpdateHashPath(navId) {
  const [elemRef, elemOnScreenResult] = useElemOnScreen();
  const history = useHistory();
  const { location } = history;
  useEffect(() => {
    if (!navId) return;
    if (!elemOnScreenResult) {
      location.hash && history.replace(location.hash.replace(navId, ''));
    } else {
      history.replace(`#${navId}`);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [elemOnScreenResult]);
  return elemRef;
}

export default useUpdateHashPath;
